<template>
  
  <div class="columns is-centered">
    <div class="column is-half" id="app">

      <hr>                                                   
            <div class="level">                                     
                <div class="level-left">                            
                    <div class="level-item">                        
                        <span class="title">Current Todos</span>    
                    </div>                                          
                </div>                                              
                <div class="level-right">                           
                    <order-select                                   
                        v-bind:loader="loading"                     
                        v-model="orderSelection"                    
                        v-on:order-changed="orderChanged">          
                    </order-select>                                 
                </div>                                              
            </div>                                                  
                                                                    
            <hr>                                                    
                                                                    
            <transition-group name="todo-list" tag="div">           
                                                                    
            <todo v-for="(todo, index) in todos"                    
                v-bind:key="todo.id"                                
                v-bind:todo="todo"                                  
                v-bind:index="index"                                
                v-on:toggle="toggleTodo"                            
                v-on:edit="editTodo"                                
                v-on:delete="deleteTodo">                           
            </todo>                                                 
                                                                    
            <create-todo-button                                     
                v-bind:key="maxId"                                  
                v-on:create-todo="addTodo">     
            </create-todo-button>                                  
                                                                    
            </transition-group>

    </div>
  </div>

</template>

<script>
import Todo from './Todo.vue'
import CreateTodoButton from './CreateTodoButton.vue'
import OrderSelect from './OrderSelect.vue'

export default {
  name: 'app',
  data () {
    return {
      todos: [],
      currentId: 1,
      maxId: 9999,
      loading: false,
      orderSelection: "created_at"
    }
  },
  mounted () {

    document.getElementById('note-input').focus();
  },
  methods: {
    orderChanged: function(order) {

        this.sortTodos();
        document.getElementById('note-input').focus();
    },
    sortTodos: function() {

        var order = this.orderSelection;
        this.todos.sort(function(a, b) {
            if(a[order] < b[order]) {
                return -1;
            } else if (a[order] > b[order]) {
                return 1;
            }
            return 0;
        });
    },
    addTodo: function(text) {

        this.todos.push({id: this.currentId, note: text, done: false, created_at: new Date().toISOString(), updated_at: new Date().toISOString()});
        this.sortTodos();
        this.currentId++;
        document.getElementById('note-input').focus();
    },
    toggleTodo: function(todo) {

        todo.done = !todo.done;

        this.editTodo(todo);
    },
    editTodo: function(todo) {

        todo.updated_at = new Date().toISOString();
        this.sortTodos();
    },
    deleteTodo: function(event) {

        var todo = event.todo;
        var index = event.index;
        this.todos.splice(index, 1);

        document.getElementById('note-input').focus();
    }
  },
  components: {
    'todo': Todo,
    'create-todo-button': CreateTodoButton,
    'order-select': OrderSelect
  }
}
</script>

<style>
.is-static {
  margin-left: 11px;
  background-color: transparent; 
}
.line-through {
  text-decoration: line-through;
}

.level:last-child {
  margin-bottom: 1.5rem;
}

.todo-list-item {
  transition: all .2s;
}

.todo-list-enter {
  opacity: 1;
}
.todo-list-leave-to {
  opacity: 0;
  transform: translateX(1rem);
}
.todo-list-leave-active {
  position: absolute;
}
.todo-list-leave-active .button {
  opacity: 0;
}
.todo-list-move {
  transition: transform .15s;
}
</style>



